@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.a {
  color: black !important;
  font-size: larger;
}
li {
  list-style-type: none;
}
.brand-logo {
  font-family: "Grand Hotel", cursive;
}
nav {
  background-color: white !important;
}

@media only screen and (min-width: 551px) {
  .mobile {
    display: none;
    color: black !important;
  }
  .sidenav {
    display: none;
  }
}
nav a {
  color: black !important;
}
.sidenav-trigger {
  margin-left: 0% !important;
}

.sidenav {
  max-width: 200px !important;
}
.btn {
  color: black !important;
  margin-top: 5px;
}

.my-card {
  max-width: 400px;
  margin: 10px auto;
  text-align: center;
  display: flex;
  align-content: center;
  margin-top: 10%;
}
.auth-card {
  padding: 20px;
}
button {
  color: white !important;
}
button:hover{
  background-color: rgb(60, 159, 221);
}
.input-field input[type="text"]:focus {
  border-bottom: 1px solid rgb(61, 110, 223) !important;
  box-shadow: 0 1px 0 0 rgb(61, 110, 223) !important;
}
.input-field input[type="password"]:focus {
  border-bottom: 1px solid rgb(61, 110, 223) !important;
  box-shadow: 0 1px 0 0 rgb(61, 110, 223) !important;
}
.lo {
  font-family: "Grand Hotel", cursive;
}
.underline {
  border-bottom: 1px solid black;
  margin: 10px auto;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

#toast-container {
  margin-bottom: 110% !important;
}

.gallary {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.item {
  max-width: 30%;
  margin-bottom: 15px;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.parent {
  max-width: 550px;
  margin: 0px auto;
}
.dp {
  height: 90%;
  width: 50%;
  max-width: 300px;
  border-radius: 56%;
}
@media only screen and (max-width: 550px) {
  .parent {
    max-width: 300px;
  }
  .dp {
    height: 50%;
    width: 50%;
    max-width: 300px;
    border-radius: 56%;
    margin-top: 20%;
  }
  .para {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .up {
    margin-bottom: 0px;
  }
  .down {
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;
  }
}
.left {
  margin-left: -20%;
}
.para {
  font-size: large;
  display: flex;
}
.up {
  margin-right: 5px;
  font-weight: bolder !important;
}
.para {
  margin-right: 20px;
}

span{
    color: white !important;
}
.input-filled input[type="text"]:focus {
    border-bottom: 1px solid rgb(61, 110, 223) !important;
    box-shadow: 0 1px 0 0 rgb(61, 110, 223) !important;
  }
